import BorderBottom from "../../../Ui/Border/BorderBottom";
import Switch from "@mui/material/Switch";
import Checkbox from "@mui/material/Checkbox";
import FormControlLabel from "@mui/material/FormControlLabel";
import style from "../../../InstituteMember/InstituteStudent/InstituteStudent.module.css";
import CreateButton from "../../../Ui/Button/CreateButton";
import { useEffect, useState } from "react";
import EditAdmissionFormSection from "./FormSection/EditAdmissionFormSection";
import AddAdmissionField from "./AddField/AddAdmissionField";
import AdmissionStudentFormSettingCardMenu from "./AddField/AdmissionStudentFormSettingCardMenu";
import QvipleLoader from "../../../Loader/QvipleLoader";
import FormTextarea from "../../../Ui/Input/FormTextarea";
import {
  useAdmissionStudentToggleChecklist,
  useAdmissionStudentUpdateSectionChecklist,
} from "../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import { useFormAddRaggingUndertaking } from "../../../Hooks/ApiHooks/InstituteStudent/institute-student-api";
import { fTypesObj } from "../../../Constant/FormSetting/form_setting_constant";

const label = { inputProps: { "aria-label": "Switch demo" } };

const AdmissionStudentFormSettingCard = ({
  st,
  onRefetch,
  formId,
  applicationId,
}) => {
  const [add, setAdd] = useState(false);
  const [addField, setAddField] = useState(false);
  const [openMenu, setOpenMenu] = useState(false);
  const [visible, setVisible] = useState(false);
  const [disabled, setDisabled] = useState(false);
  const [state, setState] = useState("");

  useEffect(() => {
    setVisible(st?.section_visibilty);
  }, [st?.section_visibilty]);

  useEffect(() => {
    if (
      [
        "undertakings",
        "antiragging_affidavit",
        "antiragging_affidavit_parents",
      ]?.includes(st?.section_key)
    ) {
      setState(st?.section_value);
    }
  }, [st?.section_key]);

  const [admissionStudentUpdateSectionChecklist] =
    useAdmissionStudentUpdateSectionChecklist();

  const [admissionStudentToggleChecklist] =
    useAdmissionStudentToggleChecklist();
  const [formAddRaggingUndertaking] = useFormAddRaggingUndertaking();

  const onToggle = () => {
    setAdd((pre) => !pre);
  };
  const onFieldToggle = () => {
    setAddField((pre) => !pre);
  };
  const onMenuToggle = () => {
    setOpenMenu((pre) => !pre);
  };

  const onToggleChanged = (e) => {
    if (st?._id && formId) {
      setDisabled((pre) => !pre);
      admissionStudentUpdateSectionChecklist({
        formId: formId,
        bodyParameter: {
          section_name: st?.section_name,
          section_key: st?.section_key,
          section_visibilty: e.target.checked,
          fsid: st?._id,
        },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };
  const onChecklistToggleChanged = (e, checkId) => {
    if (st?._id && formId && checkId) {
      setDisabled((pre) => !pre);
      admissionStudentToggleChecklist({
        formId: formId,
        bodyParameter: {
          checkID: checkId,
          fsid: st?._id,
          form_checklist_visibility: e.target.checked,
        },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  const onChecklistRequiredChanged = (checkId, flow) => {
    if (st?._id && formId && checkId) {
      setDisabled((pre) => !pre);
      admissionStudentToggleChecklist({
        formId: formId,
        bodyParameter: {
          checkID: checkId,
          fsid: st?._id,
          form_checklist_required: flow === "Yes" ? true : false,
        },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };
  const onChange = (e) => {
    setState(e.target.value);
  };
  const onUpdate = () => {
    if (applicationId && st?._id) {
      setDisabled((pre) => !pre);
      formAddRaggingUndertaking({
        bodyParameter: {
          fsid: st?._id,
          flow: "APPLICATION",
          did: applicationId,
          content: state,
        },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };
  return (
    <>
      <section className={style.sfsc_card}>
        <div className={style.sfscc_outer}>
          <div className={style.sfscc_container}>
            <Switch {...label} checked={visible} onChange={onToggleChanged} />
            <span>{st?.section_name ?? ""}</span>
          </div>
          {visible ? (
            <CreateButton
              label="edit"
              customStyle={{
                paddingBlock: "0.2rem",
                margin: "0",
              }}
              onAction={onToggle}
            />
          ) : null}
        </div>

        <div className={style.sfsccc_inner}>
          {[
            "undertakings",
            "antiragging_affidavit",
            "antiragging_affidavit_parents",
          ]?.includes(st?.section_key) ? (
            <>
              <FormTextarea
                placeholder="Please type here"
                name={st?.section_key}
                type="text"
                value={state}
                onChange={onChange}
                customFormInputContainer={{
                  width: "100%",
                  marginBlock: "0.8rem",
                }}
              />
              <CreateButton
                label="update"
                customStyle={{
                  paddingBlock: "0.2rem",
                  margin: "0",
                }}
                onAction={onUpdate}
              />
            </>
          ) : st?.section_key === "academic_details" ? (
            <>
              {st?.form_checklist?.map((chd) => (
                <div
                  className={style.sfscc_outer}
                  style={{
                    width: "100%",
                    opacity: visible ? "1" : "0.7",
                    justifyContent: "flex-start",
                    gap: "1rem",
                  }}
                >
                  <div>
                    <FormControlLabel
                      control={
                        <Checkbox
                          sx={{ color: "rgba(18, 18, 18, 0.7)" }}
                          checked={chd?.form_checklist_visibility}
                          onChange={(e) =>
                            visible
                              ? onChecklistToggleChanged(e, chd?._id)
                              : null
                          }
                        />
                      }
                      label={chd?.form_checklist_name}
                      key={chd?._id}
                    />
                    <span
                      style={{
                        marginLeft: "-10px",
                        fontSize: "12px",
                      }}
                    >
                      ({fTypesObj[chd?.form_checklist_typo]})
                    </span>
                  </div>

                  <div>
                    (
                    <span
                      style={{
                        cursor: "pointer",
                        color: chd?.form_checklist_required
                          ? "#0942a6"
                          : "rgba(18, 18, 18, 0.7)",
                        fontWeight: chd?.form_checklist_required ? 600 : 400,
                      }}
                      onClick={() =>
                        onChecklistRequiredChanged(chd?._id, "Yes")
                      }
                    >
                      Required
                    </span>{" "}
                    /{" "}
                    <span
                      style={{
                        cursor: "pointer",
                        color: !chd?.form_checklist_required
                          ? "#0942a6"
                          : "rgba(18, 18, 18, 0.7)",
                        fontWeight: !chd?.form_checklist_required ? 600 : 400,
                      }}
                      onClick={() => onChecklistRequiredChanged(chd?._id, "No")}
                    >
                      Optional
                    </span>
                    )
                  </div>
                </div>
              ))}
            </>
          ) : (
            <>
              {st?.form_checklist?.map((chd) => (
                <div
                  className={style.sfscc_outer}
                  style={{
                    width: "100%",
                    opacity: visible ? "1" : "0.7",
                    justifyContent: "flex-start",
                    gap: "1rem",
                  }}
                >
                  <div>
                    <FormControlLabel
                      control={
                        <Checkbox
                          sx={{ color: "rgba(18, 18, 18, 0.7)" }}
                          checked={chd?.form_checklist_visibility}
                          onChange={(e) =>
                            visible
                              ? onChecklistToggleChanged(e, chd?._id)
                              : null
                          }
                        />
                      }
                      label={chd?.form_checklist_lable}
                      // sx={{ ...customStyleLabel }}
                      key={chd?._id}
                    />
                    <span
                      style={{
                        marginLeft: "-10px",
                        fontSize: "12px",
                      }}
                    >
                      ({fTypesObj[chd?.form_checklist_typo]})
                    </span>
                  </div>

                  <div>
                    (
                    <span
                      style={{
                        cursor: "pointer",
                        color: chd?.form_checklist_required
                          ? "#0942a6"
                          : "rgba(18, 18, 18, 0.7)",
                        fontWeight: chd?.form_checklist_required ? 600 : 400,
                      }}
                      onClick={() =>
                        onChecklistRequiredChanged(chd?._id, "Yes")
                      }
                    >
                      Required
                    </span>{" "}
                    /{" "}
                    <span
                      style={{
                        cursor: "pointer",
                        color: !chd?.form_checklist_required
                          ? "#0942a6"
                          : "rgba(18, 18, 18, 0.7)",
                        fontWeight: !chd?.form_checklist_required ? 600 : 400,
                      }}
                      onClick={() => onChecklistRequiredChanged(chd?._id, "No")}
                    >
                      Optional
                    </span>
                    )
                  </div>

                  {/* <img
                src={`${assestsUserFinanceUrl}/menu.svg`}
                alt="menu icon"
                className={style.menu_con}
                onClick={onMenuToggle}
              /> */}
                </div>
              ))}
              {visible ? (
                <CreateButton
                  label="add_field"
                  customStyle={{
                    paddingBlock: "0.2rem",
                    margin: "0",
                  }}
                  onAction={onFieldToggle}
                />
              ) : null}
            </>
          )}
        </div>
      </section>
      <BorderBottom />
      {addField && (
        <AddAdmissionField
          onClose={onFieldToggle}
          formId={formId}
          sectionId={st?._id}
          onRefetch={onRefetch}
        />
      )}
      {add && (
        <EditAdmissionFormSection
          onClose={onToggle}
          formId={formId}
          sectionId={st?._id}
          onRefetch={onRefetch}
          data={st}
        />
      )}
      {disabled && <QvipleLoader />}
      {openMenu && (
        <AdmissionStudentFormSettingCardMenu onClose={onMenuToggle} />
      )}
    </>
  );
};

export default AdmissionStudentFormSettingCard;
