import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import Switch from "@mui/material/Switch";

import {
  existingRequiredField,
  requiredField,
} from "../../../../Validation/functions/checkingRequiredField";
import Modal from "../../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import style from "../../../../UserStaffMember/FinanceManager/Income/Income.module.css";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import CreateButton from "../../../../Ui/Button/CreateButton";
import FormInput from "../../../../Ui/Input/FormInput";
import {
  useAdmissionOneOngoingApplication,
  useOneApplicationIntakeUpdate,
} from "../../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
const label = { inputProps: { "aria-label": "Switch demo" } };

const AddIntake = ({ onClose, applicationId }) => {
  const { t } = useTranslation();
  const [errorField, setErrorField] = useState("");
  const [disabled, setDisabled] = useState(false);

  const [state, setState] = useState({
    intake_visibility: false,
    total_intake: "",
    cap_intake: "",
    il_intake: "",
  });

  const [oneApplicationIntakeUpdate] = useOneApplicationIntakeUpdate();

  const {
    admissionOneOngoingApplication,
    admissionOneOngoingApplicationRefetch,
  } = useAdmissionOneOngoingApplication({
    appId: applicationId,
    skip: !applicationId,
  });

  console.info(
    "admissionOneOngoingApplication",
    admissionOneOngoingApplication
  );

  useEffect(() => {
    if (admissionOneOngoingApplication?.admission_intake) {
      let dt = admissionOneOngoingApplication?.admission_intake;
      setState((prev) => ({
        ...prev,
        intake_visibility: dt?.intake_visibility,
        total_intake: dt?.total_intake,
        cap_intake: dt?.cap_intake,
        il_intake: dt?.il_intake,
      }));
    }
  }, [admissionOneOngoingApplication?.admission_intake]);
  const onChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };

  const onToggleChanged = (e) => {
    setState((prev) => ({
      ...prev,
      intake_visibility: e.target.checked,
    }));
  };

  const onUpdateIntake = () => {
    let validation = requiredField(state, [
      "intake_visibility",
      "total_intake",
      "cap_intake",
      "il_intake",
    ]);
    if (existingRequiredField(validation) && applicationId) {
      setDisabled((pre) => !pre);
      oneApplicationIntakeUpdate({
        appId: applicationId,
        bodyParameter: {
          admission_intake: {
            intake_visibility: state.intake_visibility,
            total_intake: state.total_intake ? +state.total_intake : 0,
            cap_intake: state.cap_intake ? +state.cap_intake : 0,
            il_intake: state.il_intake ? +state.il_intake : 0,
          },
        },
      })
        .then(() => {
          admissionOneOngoingApplicationRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    } else {
      setErrorField(validation);
    }
  };

  return (
    <Modal onClose={onClose}>
      <div className={style.modal_container}>
        <UserMemberContentWrapper>
          <div className={style.modal_container_outer_header}>
            <div className={style.modal_container_header}>
              <h6>{t("admission_intake")}</h6>
            </div>
            <img
              src={`${assestsUserFinanceUrl}/close.svg`}
              alt="close icon"
              onClick={onClose}
            />
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper
          customStyle={{
            paddingTop: "0",
          }}
        >
          <div className={style.sfscc_container}>
            <Switch
              {...label}
              checked={state.intake_visibility}
              onChange={onToggleChanged}
            />
            <span>{t("intake_visibility")}</span>
          </div>

          <FormInput
            label={t("total_intake")}
            placeholder={t("total_intake_placeholder")}
            name={"total_intake"}
            value={state.total_intake}
            type={"number"}
            onChange={onChange}
            errorShow={errorField["total_intake"]}
          />

          <FormInput
            label={t("cap_intake")}
            placeholder={t("cap_intake_placeholder")}
            name={"cap_intake"}
            value={state.cap_intake}
            type={"number"}
            onChange={onChange}
            errorShow={errorField["cap_intake"]}
          />

          <FormInput
            label={t("il_intake")}
            placeholder={t("il_intake_placeholder")}
            name={"il_intake"}
            value={state.il_intake}
            type={"number"}
            onChange={onChange}
            errorShow={errorField["il_intake"]}
          />

          <CreateButton label="update" onAction={onUpdateIntake} />
        </UserMemberContentWrapper>
        {disabled && <QvipleLoader />}
      </div>
    </Modal>
  );
};

export default AddIntake;
