import BorderBottom from "../../../Ui/Border/BorderBottom";
import style from "../../../InstituteMember/InstituteStudent/InstituteStudent.module.css";
import CreateButton from "../../../Ui/Button/CreateButton";
import { useState } from "react";
import AddFormImage from "./AddField/AddFormImage";
import FormFileView from "../../../Ui/Input/FormFileView";

const AdmissionStudentFormSettingImageCard = ({
  st,
  onRefetch,
  formId,
  applicationId,
}) => {
  const [add, setAdd] = useState(false);

  const onToggle = () => {
    setAdd((pre) => !pre);
  };

  return (
    <>
      <section className={style.sfsc_card}>
        <div className={style.sfscc_outer}>
          <div className={style.sfscc_container}>
            <span>{"Form Images"}</span>
          </div>
        </div>

        <div className={style.sfsccc_inner}>
          {st?.form_checklist?.map((ft) => (
            <FormFileView
              label={`${ft?.name}: `}
              value={
                ft?.attach
                  ? {
                      key: ft?.attach,
                    }
                  : ""
              }
            />
          ))}
          <CreateButton
            label="add_image"
            customStyle={{
              paddingBlock: "0.2rem",
              margin: "0",
              marginTop: "0.5rem",
            }}
            onAction={onToggle}
          />
        </div>
      </section>
      <BorderBottom />
      {add && (
        <AddFormImage
          onClose={onToggle}
          formId={formId}
          onRefetch={onRefetch}
          applicationId={applicationId}
        />
      )}
    </>
  );
};

export default AdmissionStudentFormSettingImageCard;
