import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import Switch from "@mui/material/Switch";

import {
  existingRequiredField,
  requiredField,
} from "../../../../Validation/functions/checkingRequiredField";
import Modal from "../../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import style from "../../../../UserStaffMember/FinanceManager/Income/Income.module.css";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import CreateButton from "../../../../Ui/Button/CreateButton";
import {
  useAdmissionOneOngoingApplication,
  useOneApplicationUpdateDocumentSetting,
} from "../../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
const label = { inputProps: { "aria-label": "Switch demo" } };

const UpdateDocument = ({ onClose, applicationId }) => {
  const { t } = useTranslation();
  const [errorField, setErrorField] = useState("");
  const [disabled, setDisabled] = useState(false);

  const [state, setState] = useState({
    collect_docs: false,
  });

  const [oneApplicationUpdateDocumentSetting] =
    useOneApplicationUpdateDocumentSetting();

  const {
    admissionOneOngoingApplication,
    admissionOneOngoingApplicationRefetch,
  } = useAdmissionOneOngoingApplication({
    appId: applicationId,
    skip: !applicationId,
  });

  //   console.info(
  //     "admissionOneOngoingApplication",
  //     admissionOneOngoingApplication
  //   );

  useEffect(() => {
    if (admissionOneOngoingApplication?.collect_docs) {
      setState((prev) => ({
        ...prev,
        collect_docs: admissionOneOngoingApplication?.collect_docs,
      }));
    }
  }, [admissionOneOngoingApplication?.collect_docs]);

  const onToggleChanged = (e) => {
    setState((prev) => ({
      ...prev,
      collect_docs: e.target.checked,
    }));
  };

  // console.info(
  //   "admissionOneOngoingApplication",
  //   admissionOneOngoingApplication
  // );
  const onUpdateIntake = () => {
    let validation = requiredField(state, ["collect_docs"]);
    if (existingRequiredField(validation) && applicationId) {
      setDisabled((pre) => !pre);
      oneApplicationUpdateDocumentSetting({
        appId: applicationId,
        bodyParameter: {
          // admission_intake: {
          collect_docs: state.collect_docs,
          // },
        },
      })
        .then(() => {
          admissionOneOngoingApplicationRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    } else {
      setErrorField(validation);
    }
  };

  return (
    <Modal onClose={onClose}>
      <div className={style.modal_container}>
        <UserMemberContentWrapper>
          <div className={style.modal_container_outer_header}>
            <div className={style.modal_container_header}>
              <h6>{t("update_document_collect_setting")}</h6>
            </div>
            <img
              src={`${assestsUserFinanceUrl}/close.svg`}
              alt="close icon"
              onClick={onClose}
            />
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper
          customStyle={{
            paddingTop: "0",
          }}
        >
          <div className={style.sfscc_container}>
            <Switch
              {...label}
              checked={state.collect_docs}
              onChange={onToggleChanged}
            />
            <span>{t("document_visibility")}</span>
          </div>

          <CreateButton label="update" onAction={onUpdateIntake} />
        </UserMemberContentWrapper>
        {disabled && <QvipleLoader />}
      </div>
    </Modal>
  );
};

export default UpdateDocument;
