import { useTranslation } from "react-i18next";
import style from "../../../UserStaffMember/FinanceManager/FinanceManager.module.css";
import { useEffect, useRef, useState } from "react";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import AdmissionStudentFormSettingCard from "./AdmissionStudentFormSettingCard";
import CreateButton from "../../../Ui/Button/CreateButton";
import AddAdmissionFormSection from "./FormSection/AddAdmissionFormSection";
import QvipleLoader from "../../../Loader/QvipleLoader";
import Loading from "../../../Loader/Loading";
import {
  useAdmissionStudentFormDetail,
  useAdmissionStudentRearrangeSection,
} from "../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import { useLocation } from "react-router-dom";
import AdmissionStudentFormSettingImageCard from "./AdmissionStudentFormSettingImageCard";
import AddIntake from "./AddIntake/AddIntake";
import UpdateDocument from "./UpdateDocument/UpdateDocument";

const AdmissionStudentFormSetting = () => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const [add, setAdd] = useState(false);
  const [resuffle, setResuffle] = useState(false);
  const [list, setList] = useState([]);
  const [disabled, setDisabled] = useState(false);
  const [docs, setDocs] = useState(null);
  const [step, setStep] = useState("");

  const dragItem = useRef();
  const dragOverItem = useRef();

  const onDragEventStart = (e, position) => {
    dragItem.current = position;
  };

  const onDragEventEnter = (e, position) => {
    dragOverItem.current = position;
  };
  const onDropItem = (e) => {
    const listCopy = [...list];
    const dragItemContentCls = listCopy[dragItem.current];
    listCopy.splice(dragItem.current, 1);
    listCopy.splice(dragOverItem.current, 0, dragItemContentCls);

    dragItem.current = null;
    dragOverItem.current = null;
    setList(listCopy);
  };

  const [admissionStudentRearrangeSection] =
    useAdmissionStudentRearrangeSection();
  const {
    admissionStudentFormDetail,
    admissionStudentFormDetailRefetch,
    admissionStudentFormDetailLoading,
  } = useAdmissionStudentFormDetail({
    data: {
      formId: getQuery.state?.formId,
    },
    skip: !getQuery.state?.formId,
  });

  useEffect(() => {
    if (admissionStudentFormDetail) {
      let arr = [];
      for (let dt of admissionStudentFormDetail) {
        if (dt?.section_key === "form_attach") {
          setDocs(dt);
        } else {
          arr.push(dt);
        }
      }
      setList(arr);
    }
  }, [admissionStudentFormDetail]);
  //   console.info("admissionStudentFormDetail", admissionStudentFormDetail);
  const onToggle = () => {
    setAdd((pre) => !pre);
  };
  const onResuffleToggle = () => {
    setResuffle((pre) => !pre);
  };

  const onStepClose = () => {
    setStep("");
  };
  const onIntakeToggle = () => {
    setStep(1);
  };
  const onDocumentToggle = () => {
    setStep(2);
  };
  const onReshuffle = () => {
    if (getQuery.state?.formId) {
      setDisabled((pre) => !pre);
      admissionStudentRearrangeSection({
        formId: getQuery.state?.formId,
        bodyParameter: {
          shuffle_arr: list,
        },
      })
        .then(() => {
          admissionStudentFormDetailRefetch();
          setDisabled((pre) => !pre);
          setResuffle(false);
        })
        .catch({});
    }
  };
  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <h6>{t("student_form")}</h6>
            <div className={style.fm_flex}>
              <CreateButton
                label="add_section"
                customStyle={{
                  paddingBlock: "0.2rem",
                  margin: "0",
                }}
                onAction={onToggle}
              />
              <CreateButton
                label={resuffle ? "save_resuffle" : "resuffle"}
                customStyle={{
                  paddingBlock: "0.2rem",
                  margin: "0",
                }}
                onAction={resuffle ? onReshuffle : onResuffleToggle}
              />
              {resuffle && (
                <CreateButton
                  label={"cancel_resuffle"}
                  customStyle={{
                    paddingBlock: "0.2rem",
                    margin: "0",
                  }}
                  onAction={onResuffleToggle}
                />
              )}
              <CreateButton
                label="add_intake"
                customStyle={{
                  paddingBlock: "0.2rem",
                  margin: "0",
                }}
                onAction={onIntakeToggle}
              />
              <CreateButton
                label="update_document"
                customStyle={{
                  paddingBlock: "0.2rem",
                  margin: "0",
                }}
                onAction={onDocumentToggle}
              />
            </div>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper>
          {list?.map((st, index) => (
            <div
              key={st?._id}
              draggable={resuffle}
              onDragStart={(e) => onDragEventStart(e, index)}
              onDragEnter={(e) => onDragEventEnter(e, index)}
              onDragEnd={onDropItem}
            >
              <AdmissionStudentFormSettingCard
                st={st}
                onRefetch={admissionStudentFormDetailRefetch}
                formId={getQuery.state?.formId}
                applicationId={getQuery.state?.applicationId}
              />
            </div>
          ))}
          <AdmissionStudentFormSettingImageCard
            st={docs}
            onRefetch={admissionStudentFormDetailRefetch}
            formId={getQuery.state?.formId}
            applicationId={getQuery.state?.applicationId}
          />
          {admissionStudentFormDetailLoading && <Loading />}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
      {disabled && <QvipleLoader />}
      {add && (
        <AddAdmissionFormSection
          onClose={onToggle}
          formId={getQuery.state?.formId}
          applicationId={getQuery.state?.applicationId}
          onRefetch={admissionStudentFormDetailRefetch}
        />
      )}
      {step === 1 ? (
        <AddIntake
          onClose={onStepClose}
          applicationId={getQuery.state?.applicationId}
        />
      ) : step === 2 ? (
        <UpdateDocument
          onClose={onStepClose}
          applicationId={getQuery.state?.applicationId}
        />
      ) : null}
    </>
  );
};

export default AdmissionStudentFormSetting;
